import React, {useState} from 'react'
import { Link } from 'react-router-dom'
import { Form, Button } from 'antd';
import './Donation.css'

const FormItem = Form.Item;

const DonationSelect = ({ logo, images, amountSelect }) => {

  const [btnClass, setBtnClass] = useState({gt:"selected",lt:"none"})
  const [amount, setAmount] = useState("gt")
  const onClick = btn => {
    const btnClass = btn === "gt" ? {gt:"selected",lt:"none"} : {gt:"none",lt:"selected"}
    const amount = btn === "gt" ? btn : "lt"
    setAmount(amount)
    setBtnClass(btnClass)
  }
  return (
    <div className="organization-data">
      <Link to="/"><img src={logo} className="logo" alt="Reintegradores logo"/></Link>
      <div className="organization-data-container">
        <h2>¡Juntos podemos hacer que las cosas sucedan!</h2>
        <img src={images[1]} alt="imagenes"/>
      </div>
      <div className="organization-data-form-container">
        <div className='green form-title'>
          <h3>Registro de Organización</h3>
        </div>
        <h4>Elegir una de las dos opciones</h4>
        <h5>que se muestran a continuación</h5>
        <Form layout="vertical" method="POST" onSubmit={e => amountSelect(e,amount)}>
          <div className="amount-select">
            <FormItem>
              <Button className={btnClass.gt} onClick={e => onClick("gt")}>
                Cantidad menor a $141,817.00
              </Button>
            </FormItem>
            <FormItem>
              <Button className={btnClass.lt} onClick={e => onClick("lt")}>
                Cantidad mayor a $141,817.00
              </Button>
            </FormItem>
          </div>
          <FormItem>
            <Button className='green' type="primary" htmlType="submit">Siguiente</Button>
          </FormItem>
        </Form>
      </div>
    </div>
  )
}

export default DonationSelect