import React, {useState, useEffect} from 'react'
import { Link } from 'react-router-dom'
import Navbar from '../general/Navbar';
import ProyectoCard from '../proyectos/ProyectoCard';
import { getOrgProjects } from '../../services/projects'
import './ProyectosOrg.css'

const img = 'https://www.nationalgeographic.com.es/medio/2018/02/27/tortuga__1280x720.jpg'

const ProyectosOrg = () => {

  const [orgProjects, setOrgProjects] = useState([])

  useEffect(() => {
    console.log('mounted or updated')
    const user = JSON.parse(localStorage.getItem('loggedUser'))
    console.log(user)
    if(!user || !user.email) setOrgProjects([])
    else {
      console.log(user._id)
      getOrgProjects(user._id)
      .then(res => {
        if(res.status === 200) {
          console.log(res)
          setOrgProjects(res.data)
        }
      })
      .catch(err => console.log(err))
    }
  },[]);

  return (
    <div>
      <Navbar logged={true} userType={"org"} />
      <div className="proyectos-org-header">
        <h2>MIS PROYECTOS</h2>
        <Link to='/proyectos/new'><span className="btn-agregar">AGREGAR PROYECTO</span></Link>
      </div>
      <div className="objective-projects">
        {orgProjects.length > 0 ?
          orgProjects.map(project => <ProyectoCard key={project._id} title={project.name}
          img={img}
          desc={project.description}/>)
          :
          <p className="not-found">No se encontraron proyectos</p>
        }
      </div>
    </div>
  )
}

export default ProyectosOrg