import axios from 'axios'
import firebase from './firebase'

const host = 'http://localhost:3000/projects'
// const host = 'https://reintegradoresdev.herokuapp.com/projects'

//upload file (Constancia Situación Fiscal, Acta Constitutiva, Curriculum, etc)
export const uploadFile=(file)=>{
  console.log('uploading file')
  const task = firebase.storage().ref('reintegradores').child(file.name).put(file)

  return task
      .then(snap=>snap.ref.getDownloadURL())
      .then(link=>link)
}

//add Project
export const addProject = project => 
  axios.post(host + '/new', project, {withCredentials:true})
    .then(r=>r.data)
    .catch(e=>e.response)

//remove Project
export const removeProject = id => 
  axios.delete(host + `/${id}`, {withCredentials:true})
    .then(r=>r.data)
    .catch(e=>e.response)

//update Project
export const updateProject = (id, data) => 
  axios.patch(host + `/${id}`, {data}, {withCredentials:true})
    .then(r=>r.data)
    .catch(e=>e.response)

//get a project
export const getProject = id => 
  axios.get(host + `/${id}`)
    .then(r=>r)
    .catch(e=>e.response)

//get projects by organization
export const getOrgProjects = id => 
axios.get(host + `/org/${id}`)
  .then(r=>r)
  .catch(e=>e.response)

//get projects by objectiv
export const getObjProjects = id => 
axios.get(host + `/obj/${id}`)
  .then(r=>r)
  .catch(e=>e.response)

//get all projects
export const getAllProjects = () => 
  axios.get(host + '/')
    .then(r=>r.data)
    .catch(e=>e.response)