import React from 'react'
import { Link } from 'react-router-dom'
import './ProyectoCard.css'

const ProyectoCard = ( { project } ) => {
  return (
    <div className="proyecto-card">
      <img src={project.img ? project.img : 'https://www.nationalgeographic.com.es/medio/2018/02/27/tortuga__1280x720.jpg'} alt="Proyecto logo"/>
      <h4>{project.name}</h4>
      <p>{project.description}</p>
      <Link to={`/proyectos/detail/${project._id}`}>Ver más &rarr;</Link>
    </div>
  )
}

export default ProyectoCard
